.effect-jazz img {
  transition: transform 0.5s ease-in-out;
}

.effect-jazz:hover img {
  transform: scale(1.1);
}

figcaption {
  animation: fadeIn 1s ease-in-out;
}

.more {
  color: #ff6b6b;
  font-weight: bold;
  cursor: pointer;
}

.more:hover {
  text-decoration: underline;
}
