#visual {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  position: relative;
  /* max-width: 2100px; */
}

#visual #video-container {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 140vw;
  /* 크기를 더 키워서 전체 화면 채우기 */
  height: 160vh;
  transform: translate(-50%, -50%);
  pointer-events: none;
  /* 클릭 방지 */
  position: relative;
}


/* 모바일 비쥬얼 감춤 */
#visual div.swiper-container div.swiper-wrapper div.swiper-slide video.mobile {
  display: none;
}

#visual div.swiper-pagination {
  display: none;
}




/* c01 */
#c01 {
  width: 100%;
  height: auto;
  /* border: 10px solid hotpink; */
  overflow: hidden;
  /* padding: 187px 0 184px; */
}

#c01 div.inner {
  width: 74.06%;
  height: 100%;
  padding: 13.15vw 0 12.94vw;
  /* padding: 187px 0 183px; */
  /* border: 10px solid rgb(158, 255, 105); */
  margin: 0 auto;
  overflow: hidden;
  display: flex;
  justify-content: center;
}



/* 제품비교 */
#c01 div.inner div.pd_comparison {
  width: 22.64%;
  height: auto;
  /* border: 1px solid rgb(125, 24, 226); */
  display: block;
  /* margin: 0 10px 0 0; */
  margin: 0 0.7vw 0 0;
  /* border-radius: 10px; */
  /* box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); */
  overflow: hidden;


}

#c01 div.inner div.pd_comparison figure {
  /* border: 1px solid rgb(175, 236, 236); */
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.3s ease-in-out;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 10px;


}

#c01 div.inner div.pd_comparison figure img {
  object-fit: cover;
  object-position: 50% 0;
  width: 100%;
  height: 100%;
  transition: transform 0.3s ease-in-out;

}

#c01 div.inner div.pd_comparison figure:hover img {
  transform: scale(1.1);
}

#c01 div.inner div.pd_comparison figure:hover {
  transform: scale(1.02);
}

#c01 div.inner div.pd_comparison figure figcaption {
  /* border: 1px solid rgb(255, 5, 5); */
  color: black;
  font-size: 24px;
  font-family: 'Hancom Gothic', sans-serif;

  font-weight: bold;
  position: absolute;
  top: 46px;
  left: 35px;
}

#c01 div.inner div.pd_comparison figure:hover,
#c01 div.inner div.pd_comparison figure figcaption {
  opacity: 1;
}

/* 글씨 animate css */
#c01 div.inner div.pd_comparison figure:hover figcaption {
  /* border: 1px solid rgb(255, 5, 5); */
  font-weight: bold;
  position: absolute;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  z-index: 2;
}

#c01 div.inner div.pd_comparison figure:hover figcaption {
  opacity: 1;
  animation: tada;
  animation-duration: 0.5s;
  /* 자리가 옮겨지는 현상 */
  animation-fill-mode: forwards;
}


