header {
  width: 100%;
  height: 80px;
  top: 0;
  left: 0;
  z-index: 100;
  position: fixed;
  /* border: 5px solid rgb(255, 238, 190); */

}
#header.scrolled {
  background-color: rgba(241, 241, 241, 0.9);
  height: 80px;
  /* Adjust as needed */
  transition: background-color 0.3s ease-in-out;
}

.sub01-header, .sub02-header, .sub03-header {
  /* position: fixed; */
  height: 80px;
  background: #fff;
}

/* header:hover div.inner {
  height: 250px;
  background: rgba(0, 0, 0, 0.6);
  opacity: 1;
} */


header div.inner {
  width: 92.24%;
  height: 100%;
  /* border: 5px solid rgb(255, 238, 190); */
  margin: 0 auto;
  position: relative;
  padding: 10px 0;
  max-width: 2300px;
  background-color: transparent;
  transition: background-color 0.3s ease, padding 0.3s ease;
  z-index: 1000;
  
}




header div.inner h1.pc-logo {
  /* border: 5px solid gainsboro; */
  display: block;
  position: absolute;
  top: 25px;

}

header div.inner h1.pc-logo a {
  /* border: 5px solid rgb(197, 255, 180); */
  width: 40px;
  height: 40px;
  display: block;
  background: url(../assets/images/logo.gif) no-repeat 0 0;
  background-size: 50%;

}

header div.inner h1.pc-logo span {
  display: none;
}

header div.inner nav {
  /* border: 5px solid rgb(255, 66, 66); */
  width: 100%;
  height: 100%;
  margin: 0 auto;
}

header div.inner nav ul {
  /* border: 5px solid rgb(236, 233, 36); */
  /* width: 100%; */
  height: 100%;
  /* display: inline-block; */
  margin: 0 auto;
  text-align: center;


}

header div.inner nav ul li {
  display: inline-block;
  /* padding-left: 0; */
  /* border: 5px solid rgb(36, 236, 159); */
  position: relative;
  height: 100%;
}

header div.inner nav ul li:hover{
 /* border-bottom: 3px solid rgb(97, 150, 201); */
 transition: width 0.3s ease-in-out, left 0.3s ease-in-out;
 /* background: rgba(155, 155, 155, 0.1); */
 /* color: #6f84c8; */
 /* background: #6f84c8; */

}




header:hover .submenu {
  opacity: 1;
  visibility: visible;
  
}


header div.inner nav ul li a {
  width: 150px;
  height: 54px;
  font-size: 16px;
  /* border: 3px solid rgb(6, 73, 6); */
  display: block;
  text-align: center;
  margin: 0 auto;
  padding-top: 14px;
  font-family: "Gowun Dodum", sans-serif;
  font-weight: 600;
  /* background:rgb(255, 255, 242); */
}
header div.inner nav ul li a:hover {
 border-bottom: 3px solid rgb(97, 150, 201);

  color: #6f84c8;
}


     /* 서브메뉴 기본 스타일: 처음엔 보이지 않도록 설정 */
header div.inner nav ul li ul.submenu{
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background: #ffffff;
  /* box-shadow: 0 2px 5px rgba(172, 172, 172, 0.1); */
  /* box-shadow: 0 2px 5px rgb(red, green, blue); */

  /* border: 5px solid rgb(40, 163, 44); */
  height: auto;
  

}
/* 서브메뉴 항목 스타일 */
header div.inner nav ul li ul.submenu li{
  margin:0;
  /* border: 5px solid rgb(99, 40, 163); */
  width: 100%;
}
header div.inner nav ul li ul.submenu li a{
  display: block;
  padding: 17px 20px;
  color: #333;
  white-space: nowrap;
  width: 100%;
  /* border: 5px solid antiquewhite; */
}
header div.inner nav ul li ul.submenu li a:hover{
  /* background-color: #6f84c8; */
  /* border: 5px solid rgb(19, 176, 45); */
 border-bottom: 3px solid rgb(27, 175, 59);
 /* background-color: none; */



}
      /* 마우스 오버 시 서브메뉴 보이기 */
header div.inner nav ul li:hover ul.submenu{
  display: block;
  /* border: 2px solid rgb(255, 255, 255); */

}







