/* -------- FOOTER :: 상단 -------- */
#footer {
  background-color: #2f2f2f;
  height: auto;
  /* border: 10px solid rgb(162, 214, 18); */
 
}

#footerTop {
  padding: 50px 0 20px;
  /* border: 10px solid violet; */
  height: auto;


}

.footer-contact-con {
  width: 100%;
  height: 427px;
  display: flex;
  justify-content: space-between;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  /* border: 1px solid rgb(255, 255, 255);
  background:#fff; */
  
}

.footer-contact-left {
  /* width: 40%; */
  /* border: 5px solid #ff5858; */
  height: 100%;
  margin-left: 136px;
  padding-top: 30px;

}

.footer-contact-left .en-tit {
  /* margin-top: 25px; */
  font-size: 1.6rem;
  font-weight: 600;
  color: #6f84c8;
  /* border: 1px solid rgb(88, 238, 80); */
}

.footer-contact-left .tit {
  margin-top: 30px;
  font-size: 3rem;
  line-height: 1.28;
  font-weight: 300;
  letter-spacing: -0.75px;
  color: #989898;
  /* border: 1px solid rgb(255, 244, 145); */
}

.footer-contact-left .tit b {
  font-weight: 600;
  color: #fff;
  /* border: 1px solid rgb(218, 21, 21); */
}

.footer-contact-left .txt {
  margin-top: 40px;
  margin-bottom: 20px;
  font-size: 1rem;
  line-height: 1.68;
  font-weight: 300;
  letter-spacing: -0.65px;
  color: #8c8c8c;
  /* border: 1px solid rgb(255, 149, 255); */
}

/*  풋터 오른쪽 */
.footer-contact-right {
  width: 800px;
  height: 100%;
  /* border: 10px solid rgb(87, 255, 3); */
  margin-right: 136px;
  


}

.footer-contact-list {

  /* border: 3px solid rgb(255, 68, 68); */
  overflow: hidden;
  /* height: 200px; */

}

.footer-contact-item {
  position: relative;
  float: left;
  width: 50%;
  /* margin: 12px 10px; */
  padding: 0 15px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  overflow: hidden;
  /* border: 3px solid rgb(252, 255, 253); */
}
.footer-contact-item:nth-child(5) {
  width: 100%;
}

.footer-contact-item.half {
  width: 50%;

  /* border: 3px solid rgb(220, 216, 13); */
}

.footer-contact-item:before {
  position: absolute;
  bottom: 0;
  left: 0;
  content: '';
  width: 100%;
  height: 2px;
  background-color: rgba(255, 255, 255, 0.2);
  /* border: 5px solid gold; */
}
/* .footer-contact-item:nth-child(5):before {
  border: 5px solid rgb(24, 212, 52);
  width: 100%;
} */

.footer-contact-item:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #2f2f2f;
  display: block;
}

.footer-contact-item .tit {
  float: left;
  /* border: 1px solid chartreuse; */
  width: 80px;
  font-size: 1.12rem;
  line-height: 60px;
  letter-spacing: -0.65px;
  font-weight: 300;
  color: #c0c0c0;
}

.footer-contact-item .tit.long {
  width: 150px;
  /* border: 1px solid rgb(94, 255, 148); */
}

.footer-contact-item .tit.full {
  width: 100%;

  /* border: 1px solid rgb(195, 255, 0); */
}

.footer-contact-item .tit span {
  margin-left: 3px;
  color: #fcc038;
}

.footer-contact-item .tit em {
  margin-left: 5px;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.5);
  /* border: 1px solid rgb(255, 167, 255); */
}

.footer-contact-item .tit+input {
  float: left;
  width: calc(100% - 100px);
  height: 60px;
  border: 0;
  background-color: transparent;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-border-radius: 0;
  /* border: 5px solid rgb(255, 81, 81); */
}

.footer-contact-item .tit+input::-ms-clear {
  display: none;
}

.footer-contact-item .tit.long+input {
  width: 200px;
  /* border: 1px solid rgb(255, 167, 255); */
  /* height: 50px; */
}

.footer-contact-item .tit+textarea {
  width: 100%;
  height: 80px;
  border: 0;
  background-color: transparent;
  resize: none;

  -webkit-appearance: none;
  -webkit-border-radius: 0;
  /* border: 1px solid rgb(252, 38, 10); */
}



.footer-contact-btn {
  position: relative;
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* border: 1px solid rgb(101, 255, 122); */
}



.footer-contact-btn .agree-check-box {
  /* border: 1px solid palegreen; */
}

.footer-contact-btn .agree-check-box label {
  display: inline-block;
  vertical-align: middle;
  color: #fff;
  font-size: 18px;
  line-height: 2;
  letter-spacing: -0.65px;
  font-weight: 300;
  /* border: 1px solid rgb(154, 238, 197); */
}

.footer-contact-btn .agree-check-box label a {
  color: inherit;
  /* color: #848484; */
  display: inline-block;
  border-bottom: 1px solid #fff;
  font-size: 18px;
}



.footer-contact-btn .agree-check-box input {
  /* display: none */
  width: 15px;
  height: 15px;
  /* border: 10px solid rgb(80, 230, 95); */


}

.footer-contact-btn .agree-check-box input:checked+label i {
  color: #fff;
  /* border: 10px solid rgb(80, 230, 95); */

}

.footer-contact-btn button {
  width: 183px;
  height: 62px;
  font-size: 20px;
  font-weight: 600;
  letter-spacing: -0.65px;
  color: #ffffff;
  background-color: #6f84c8;
  margin-right: 0;
}

.footer-contact-right.animated .footer-contact-item,
.footer-contact-right.animated .footer-contact-btn {
  animation: AniOpa both 0.8s 0.2s;
}

.footer-contact-right.animated .footer-contact-item:after,
.footer-contact-right.animated .footer-contact-btn:after {
  animation: AniLeft both 0.8s 0.2s;
}

.footer-contact-right.animated .footer-contact-item:nth-child(3),
.footer-contact-right.animated .footer-contact-item:nth-child(3):after,
.footer-contact-right.animated .footer-contact-item:nth-child(4),
.footer-contact-right.animated .footer-contact-item:nth-child(4):after {
  animation-delay: 0.5s;
}

.footer-contact-right.animated .footer-contact-item:nth-child(5),
.footer-contact-right.animated .footer-contact-item:nth-child(5):after {
  animation-delay: 0.8s;
}

.footer-contact-right.animated .footer-contact-btn,
.footer-contact-right.animated .footer-contact-btn:after {
  animation-delay: 1.2s;
}

@keyframes AniOpa {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 0;
  }

  51% {
    opacity: 1;
  }

  100% {
    opacity: 1;
  }
}

@keyframes AniLeft {
  0% {
    transform: translateX(-101%);
  }

  50% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(101%);
  }
}
